import React, {PureComponent} from 'react'
import {Form, Button, Row, Col, message} from 'antd'
import {CommonSelect} from 'Form'
import {formItemLayout} from 'enum'
import style from '../style/Form.scss'
import {connect} from 'react-redux'
import {bindActionCreators} from 'redux'
import {setBaseState, handleOrderGroupCodeFocus, handleOrderGroupCodeSearch, handleCommodityCodeSearch, handleSearchResult} from '../redux/reducer'

@connect(
  state => ({...state.splitGroupModel}),
  dispatch => bindActionCreators({setBaseState, handleOrderGroupCodeFocus, handleOrderGroupCodeSearch, handleCommodityCodeSearch, handleSearchResult}, dispatch)
)
class App extends PureComponent {
  constructor() {
    super()
  }

  /* 搜索查询 */
  handleSearch = () => {
    const {form: {validateFieldsAndScroll}, handleSearchResult, setBaseState} = this.props
    validateFieldsAndScroll((error, values) => {
      if (!error) {
        const searchParams = {}
        if (values.inventoryDcCode && values.inventoryDcCode.length > 0) searchParams.inventoryDcCode = values.inventoryDcCode
        if (values.categoryCodes && values.categoryCodes.length > 0) searchParams.categoryCodes = values.categoryCodes
        if (values.groupCode && values.groupCode.length > 0) searchParams.groupCode = values.groupCode
        if (values.categoryType && values.categoryType.length > 0) searchParams.categoryType = values.categoryType
        setBaseState({searchParams})
        handleSearchResult(searchParams)
      }
    })
  }

  /* 分组 查询 */
  handleOrderGroupCodeSearch = (searchKey = '*') => {
    if (!searchKey) return false
    try {
      this.props.handleOrderGroupCodeSearch(searchKey)
    } catch (err) {
      console.error(err)
    }
  }

  /* 搜索重置 */
  handleReset = () => {
    this.props.setBaseState({resetList_groupCode: {}, resetList_categoryCode: {}, searchParams: {}})
    this.props.form.resetFields()
  }

  /* 商品编码 查询 */
  handleCommodityCodeSearch = (searchKey = '*') => {
    if (!searchKey) return false
    try {
      const values = this.props.form.getFieldsValue(),
        categoryType = values.categoryType,
        inventoryDcCode = values.inventoryDcCode
      if (!categoryType) return message.error('请先选择类型')
      if (categoryType === '0') {
        this.props.handleCommodityCodeSearch({searchKey, inventoryLocation: inventoryDcCode}, categoryType)
      } else {
        const searchParams = {
          fuzzyWord: searchKey,
          level: categoryType == '1' ? 4 : categoryType == '2' ? 3 : categoryType == '3' ? 2 : 0 // 后端类型对应大类值；2大类 3中类 4小类
        }
        this.props.handleCommodityCodeSearch(searchParams, categoryType)
      }
    } catch (err) {
      console.error(err)
    }
  }

  /* 库存地点 内容变化 */
  /* 类型 内容变化 */
  handleChange = type => value => {
    const {form: {setFieldsValue}, setBaseState} = this.props
    if (type == 'inventoryDcCode') {
      setBaseState({resetList_groupCode: {}, groupCodeListCOnfig: null})
      setFieldsValue({groupCode: undefined})
    } else if (type == 'categoryType') {
      setBaseState({resetList_categoryCode: {}, goodsListConfig: null})
      setFieldsValue({categoryCode: undefined})
    }
  }

  render() {
    const {form: {getFieldDecorator}, resetList_groupCode, resetList_categoryCode, dcListConfig, groupCodeListCOnfig, categoryTypeListConfig, goodsListConfig} = this.props
    return (
      <Form className={style.form}>
        <Row type="flex" justify="space-between">
          <Col span={6}>
            <CommonSelect
              getFieldDecorator={getFieldDecorator}
              label="库存地点"
              decorator="inventoryDcCode"
              showSearch
              listConfig={dcListConfig}
              placeholder="请选择"
              listRenderGetValue={item => item.dcCode}
              listRenderGetLabel={item => `${item.dcCode} ${item.dcName}`}
              listRenderGetShow={item => `${item.dcName}`}
              onChange={this.handleChange('inventoryDcCode')}
              {...formItemLayout}
            />
          </Col>
          <Col span={6}>
            <CommonSelect
              getFieldDecorator={getFieldDecorator}
              label="分组"
              decorator="groupCode"
              showSearch
              listConfig={groupCodeListCOnfig}
              filterOption={false}
              placeholder="请选择"
              listRenderGetValue={item => item.groupCode}
              listRenderGetLabel={item => `${item.groupCode} ${item.groupName}`}
              listRenderGetShow={item => `${item.groupName}`}
              resetList={resetList_groupCode}
              onFocus={this.props.handleOrderGroupCodeFocus}
              onSearch={this.handleOrderGroupCodeSearch}
              {...formItemLayout}
            />
          </Col>
          <Col span={6}>
            <CommonSelect
              getFieldDecorator={getFieldDecorator}
              decorator="categoryType"
              listConfig={categoryTypeListConfig}
              label="类型"
              placeholder="请选择"
              listRenderGetValue={item => item.code}
              listRenderGetLabel={item => `${item.value}`}
              onChange={this.handleChange('categoryType')}
              {...formItemLayout}
            />
          </Col>
          <Col span={6}>
            <CommonSelect
              showSearch
              filterOption={false}
              mode='tags'
              getFieldDecorator={getFieldDecorator}
              listConfig={goodsListConfig}
              decorator="categoryCodes"
              label="商品/分类"
              placeholder="编码/名称"
              tokenSeparators={[',', ' ', '\n']}
              listRenderGetValue={item => item.productCode || item.categoryCode}
              listRenderGetLabel={item => `${item.productCode || item.categoryCode}/${item.productName || item.categoryName}`}
              resetList={resetList_categoryCode}
              onFocus={this.handleCommodityCodeSearch}
              onSearch={this.handleCommodityCodeSearch}
              {...formItemLayout}
            />
          </Col>
          <Col span={12}/>
          <Col span={6}>
            <Row type="flex" justify="end" className={style.mb15}>
              <Button type="primary" className={style.mr10} onClick={this.handleSearch} >查询</Button>
              <Button onClick={this.handleReset}>重置</Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



