import React, {Component} from 'react'
import {
  Form as NativeForm,
  message,
  Modal,
  Button
} from 'antd'
import AddPopupForm from './AddPopupForm'
import {connect} from 'react-redux'
import {bindActionCreators} from 'redux'
import {setBaseState, showAddPopup, submitAddSplitGroupData} from '../redux/reducer'

@connect(
  state => ({...state.splitGroupModel}),
  dispatch => bindActionCreators({setBaseState, showAddPopup, submitAddSplitGroupData}, dispatch)
)
class Form extends Component {
  constructor(props) {
    super(props)
    this.saveName = {} // 修改需保存名称
  }

  /* 存对应名称 */
  handleSaveName = (key, value) => {
    this.saveName[key] = value
  }

  /* 提交 */
  handleSubmit = () => {
    const {form: {validateFieldsAndScroll}, submitAddSplitGroupData} = this.props
    validateFieldsAndScroll((err, values) => {
      if (err) return
      try {
        const params = {
          businessType: 1001, // 大宗类型
          categoryType: values.categoryType || '', // 类型
          inventoryDcCode: values.inventoryDcCode && values.inventoryDcCode.split('\/')[0] || '',
          groupCode: values.groupCode && values.groupCode.split('\/')[0] || '',
          categoryCode: values.categoryCode && values.categoryCode.split('\/')[0] || '',
          ...this.saveName
        }
        if (params.businessType == '' || params.categoryType == '' || params.inventoryDcCode == '' || params.groupCode == '' || params.categoryCode == '') return message.error('请输入必填内容')
        for (let idx in params) {
          if (params[idx] == '') delete params[idx]
        }
        submitAddSplitGroupData(params)
      } catch (err) {
        console.error(err)
      }
    })
  }

  render() {
    const {showAddPopup, isShowAddPopUp, loading} = this.props

    return (
      <Modal
        style={{top: '10px'}}
        visible={isShowAddPopUp}
        onOk={this.handleSubmit}
        onCancel={() => showAddPopup(false)}
        title='新增'
        footer={[
          <Button key="back" onClick={() => showAddPopup(false)}>取消</Button>,
          <Button key="submit" type="primary" loading={loading} onClick={this.handleSubmit}>
            确定
          </Button>
        ]}
      >
        <AddPopupForm
          handleSaveName={this.handleSaveName}
          saveName={this.saveName}
          {...this.state}
          {...this.props}
        />
    </Modal>)
  }
}

export default NativeForm.create()(Form)



