import React, {PureComponent} from 'react'
import {
  Button,
  message
} from 'antd'
import {
  API_POST_CONFIGURE_SPLIT_ORDER_GROUPS_IMPORT,
  API_GET_CONFIGURE_SPLIT_ORDER_GROUPS_DOWNLOAD,
  API_GET_CONFIGURE_SPLIT_ORDER_GROUPS_EXPORT_ERROR,
  API_DELETE_CONFIGURE_SPLIT_ORDER_GROUPS_DELETE
} from 'api'
import {getData} from 'util'
import {
  getVariantSession,
  LOGIN_TOKEN
} from 'Variant'
import {Popup, Import} from 'HOC'
import style from '../style/Tool.scss'
import {connect} from 'react-redux'
import {bindActionCreators} from 'redux'
import {setBaseState, handleOrderGroupCodeFocus, handleOrderGroupCodeSearch, handleCommodityCodeSearch, handleSearchResult, exportDatas, showAddPopup} from '../redux/reducer'

@connect(
  state => ({...state.splitGroupModel}),
  dispatch => bindActionCreators({setBaseState, handleOrderGroupCodeFocus, handleOrderGroupCodeSearch, handleCommodityCodeSearch, handleSearchResult, exportDatas, showAddPopup}, dispatch)
)
export default class TableTool extends PureComponent {
  constructor() {
    super()

    this.columns = [{
      title: '商品编码',
      dataIndex: 'productCode',
      key: 'productCode',
      width: 150
    }, {
      title: '发货方编码',
      dataIndex: 'supplierCode',
      key: 'supplierCode',
      width: 150
    }, {
      title: '子客户号',
      dataIndex: 'sapSubCode',
      key: 'sapSubCode',
      width: 150
    }, {
      title: '报错原因',
      dataIndex: 'message',
      key: 'message',
      width: 150
    }]
  }

  /* 删除 按钮 */
  handleDetaile = () => {
    const {selectedRows, setBaseState} = this.props

    if (!selectedRows.length) return message.error('至少选中一项')

    setBaseState({visible: true, type: '0'})
  }

  /*--------- 弹出框 ---------- */
  /* 删除 确定按钮 */
  handleDeleteOk = () => {
    const {selectedRows, handleSearchResult, setBaseState} = this.props

    if (!selectedRows.length) return message.error('至少选中一项')

    let ids = selectedRows.map(item => item.id).join()
    getData({api: API_DELETE_CONFIGURE_SPLIT_ORDER_GROUPS_DELETE, searchParams: {ids}}).then(data => {
      this.handleCancel()
      if (data && data.message == 'success' && !data.result) {
        message.success('删除成功')
        handleSearchResult()
        setBaseState({selectedRowKeys: [], selectedRows: []})
      } else if (data.result != '') {
        message.error(data.result)
      }
    })
  }

  /* 取消按钮 */
  handleCancel = () => this.props.setBaseState({visible: false})

  /* 提示框类型选择 */
  switchType = () => (
    <Popup
      visible={this.props.visible}
      okType={'danger'}
      onOk={this.handleDeleteOk}
      onCancel={this.handleCancel}
      content={(<p style={{textIndent: '40px'}}>是否确认删除?</p>)}
    />
  )
  /*--------- 弹出框 ---------- */

  /* 下载模版 */
  handleDownLoad = () => getData({api: API_GET_CONFIGURE_SPLIT_ORDER_GROUPS_DOWNLOAD})

  /* 导入成功 */
  handleImportSuccess = res => {
    res && res.message === 'success' && message.success('导入成功')
    this.props.handleSearchResult()
  }

  // 导入状态变更
  importChange = ({file: {status}}) => {
    const {setBaseState} = this.props
    if (status === 'uploading') setBaseState({uploading: true})
    else setBaseState({uploading: false})
  }

  /* 报错导出 */
  handleExprotError = () => {
    getData({api: API_GET_CONFIGURE_SPLIT_ORDER_GROUPS_EXPORT_ERROR, searchParams: {loginToken: getVariantSession(LOGIN_TOKEN)}})
  }

  render() {
    const {exportDatas, eloading, uploading, visible, showAddPopup} = this.props
    return (
      <div className={style['table-tool']}>
        <Button className={style.btn} type="primary" onClick={() => showAddPopup(true)}>新增</Button>
        <Button className={style.btn} type="primary" onClick={this.handleDetaile}>删除</Button>
        <Button className={style.btn} type="primary" onClick={this.handleDownLoad}>下载模版</Button>
        <Import
          action={{api: API_POST_CONFIGURE_SPLIT_ORDER_GROUPS_IMPORT}}
          onOk={this.handleImportSuccess}
          onExprotError={this.handleExprotError}
          onChange={this.importChange}
        >
          <Button loading={uploading} className={style.btn} type="primary">导入数据</Button>
        </Import>
        <Button onClick={exportDatas} loading={eloading} className={style.btn} type="primary">导出数据</Button>
        {visible && this.switchType()}
      </div>
    )
  }
}



