import React, {PureComponent} from 'react'
import {Bread} from 'Bread'
import {Form, Table, Tool, AddPopup} from './components'
import {connect} from 'react-redux'
import {bindActionCreators} from 'redux'
import {handleSearchResult} from './redux/reducer'

@connect(
  (state) => ({...state.splitGroupModel}),
  (dispatch) => bindActionCreators({handleSearchResult}, dispatch)
)
export default class App extends PureComponent {
  constructor() {
    super()
  }

  componentWillMount() {
    this.props.handleSearchResult()
  }

  render() {
    return (
      <section>
        <Bread route={['配置模块', '拆单分组']} />
        <Form />
        <Tool />
        <Table />
        <AddPopup />
      </section>
    )
  }
}



