import React, {PureComponent} from 'react'
import {
  message
} from 'antd'
import {
  CommonSelect
} from 'Form'
import {
  API_GET_CLIENT_AGREEENT_GOODS_CATEGORY_TYPE,
  API_GET_GOODS_INFO_ADD_GOODS,
  API_GET_CLIENT_AGREEENT_GOODS_TYPE_CODE,
  API_GET_PROMOTION_DETAILS_INVENTORY_DCS,
  API_GET_PICKING_SORTING_WORKERS_ORDER_GROUP
} from 'api'
import {popupFormItemLayout} from 'enum'
import style from '../style/AddPopup.scss'

export default class AddPopupForm extends PureComponent {
  constructor(props) {
    super(props)

    this.state = {
      resetList_goods: {}, // 重置下拉框options
      goodsListConfig: null, // 商品
      groupCodeListCOnfig: null, // 分组
      categoryTypeListConfig: {api: API_GET_CLIENT_AGREEENT_GOODS_CATEGORY_TYPE}, // 类型
      categoryTypeValue: '0', // 类型值
      sapSubCodeList: [], // 子客户
      dcListConfig: {api: API_GET_PROMOTION_DETAILS_INVENTORY_DCS} // 发货方接口配置
    }
  }
  /** 选中分组时 */
  handleGroupCodeFocus = () => {
    this.setState({groupCodeListCOnfig: {api: API_GET_PICKING_SORTING_WORKERS_ORDER_GROUP, searchParams: {
      groupName: '',
      size: 20
    }}})
  }
  /* 分组 查询 */
  handleOrderGroupCodeSearch = (searchKey = '') => {
    if (!searchKey) return false
    try {
      this.setState({groupCodeListCOnfig: {api: API_GET_PICKING_SORTING_WORKERS_ORDER_GROUP, searchParams: {
        groupName: searchKey,
        size: 20
      }}})
    } catch (err) {
      console.error(err)
    }
  }

  /* 商品编码 查询 */
  handleCommodityCodeSearch = searchKey => {
    if (!searchKey) return false
    try {
      let values = this.props.form.getFieldsValue(),
        categoryType = values.categoryType,
        inventoryDcCode = values.inventoryDcCode && values.inventoryDcCode.split('\/')[0]

      if (!categoryType) return message.error('请先选择类型')

      switch (categoryType) {
        case '0' : {
          if (!inventoryDcCode) return message.error('请先选择库存地点')

          this.setState({goodsListConfig: {api: API_GET_GOODS_INFO_ADD_GOODS, searchParams: {searchKey, locationCode: inventoryDcCode}}})
          break
        }
        case '1' :
        case '2' :
        case '3' : {
          let searchParams = {
            fuzzyWord: searchKey,
            level: categoryType == '1' ? 4 : categoryType == '2' ? 3 : categoryType == '3' ? 2 : 0 // 后端类型对应大类值；2大类 3中类 4小类
          }

          this.setState({goodsListConfig: {api: API_GET_CLIENT_AGREEENT_GOODS_TYPE_CODE, searchParams}})
          break
        }
      }
    } catch (err) {
      console.error(err)
    }
  }

  /* 履约地点 内容变化 */
  /* 履约地点 内容变化 */
  /* 客户名称 内容变化 */
  handleChange = (type, value) => {
    const {
      form: {setFieldsValue},
      saveName
    } = this.props

    // 去除重复选择
    let fieldsValue = saveName[type]
    if (!value || value == '' || typeof value !== 'string' || fieldsValue == value) return false

    switch (type) {
      case 'inventoryDcCode' : { // 库存地点
        this.setState({
          goodsListConfig: null,
          resetList_goods: {}
        }, () => {
          setFieldsValue({
            groupCode: undefined,
            categoryType: undefined,
            categoryCode: undefined
          })
          this.handleSelect(type, value)
        })
        break
      }
      case 'groupCode' : { // 分组
        this.setState({
          goodsListConfig: null,
          resetList_goods: {}
        }, () => {
          setFieldsValue({
            categoryType: undefined,
            categoryCode: undefined
          })
          this.handleSelect(type, value)
        })
        break
      }
      case 'categoryType' : { // 类型
        this.setState({
          goodsListConfig: null,
          resetList_goods: {}
        }, () => {
          setFieldsValue({
            categoryCode: undefined
          })
        })
        break
      }
      case 'categoryCode': { // 商品/分类
        this.handleSelect(type, value)
        break
      }
    }
  }

  /* 履约地点 选择 */
  /* 分组 选择 */
  /* 商品/分类 选择 */
  handleSelect = (type, value) => {
    const {
      handleSaveName,
      saveName
    } = this.props

    // 去除重复选择
    let fieldsValue = saveName[type]
    if (!value || value == '' || typeof value !== 'string' || fieldsValue == value) return false

    let name = ''
    try {
      name = value.split('\/').pop()
    } catch (err) {
      console.error(err)
    }

    if (type === 'inventoryDcCode') {
      handleSaveName('inventoryDcName', name)
    } else if (type === 'groupCode') {
      handleSaveName('groupName', name)
    } else if (type === 'categoryCode') {
      handleSaveName('categoryName', name)
    }
  }

  render() {
    const {
      form: {getFieldDecorator}
    } = this.props
    const {
      goodsListConfig,
      groupCodeListCOnfig,
      dcListConfig,
      resetList_sap,
      resetList_goods,
      categoryTypeListConfig
    } = this.state

    return (
      <div className={style.goodsNewPrieSettings_addCommodityPrices}>
        <CommonSelect
          getFieldDecorator={getFieldDecorator}
          label="库存地点"
          decorator="inventoryDcCode"
          showSearch
          // mode="multiple"
          listConfig={dcListConfig}
          placeholder="请选择"
          listRenderGetValue={item => `${item.dcCode}/${item.dcName}`}
          listRenderGetLabel={item => `${item.dcCode}/${item.dcName}`}
          listRenderGetShow={item => `${item.dcName}`}
          rules={[{required: true, message: '库存地点不可为空'}]}
          onChange={this.handleChange.bind(this, 'inventoryDcCode')}
          {...popupFormItemLayout}
        />
        <CommonSelect
          getFieldDecorator={getFieldDecorator}
          label="分组"
          decorator="groupCode"
          showSearch
          filterOption={false}
          listConfig={groupCodeListCOnfig}
          onFocus={this.handleGroupCodeFocus}
          placeholder="请选择"
          listRenderGetValue={item => `${item.groupCode}/${item.groupName}`}
          listRenderGetLabel={item => `${item.groupCode}/${item.groupName}`}
          listRenderGetShow={item => `${item.groupName}`}
          resetList={resetList_sap}
          rules={[{required: true, message: '分组不可为空'}]}
          onChange={this.handleChange.bind(this, 'groupCode')}
          onSearch={this.handleOrderGroupCodeSearch}
          // onFocus={this.handleOrderGroupCodeSearch}
          {...popupFormItemLayout}
        />
        <CommonSelect
          getFieldDecorator={getFieldDecorator}
          decorator="categoryType"
          listConfig={categoryTypeListConfig}
          label="类型"
          placeholder="请选择"
          onChange={this.handleChange.bind(this, 'categoryType')}
          rules={[{required: true, message: '类型不可为空'}]}
          listRenderGetValue={item => item.code}
          listRenderGetLabel={item => `${item.value}`}
          {...popupFormItemLayout}
        />
        <CommonSelect
          showSearch
          filterOption={false}
          getFieldDecorator={getFieldDecorator}
          listConfig={goodsListConfig}
          onFocus={this.handleCommodityCodeSearch.bind(this, '*')}
          onSearch={this.handleCommodityCodeSearch}
          onChange={this.handleChange.bind(this, 'categoryCode')}
          resetList={resetList_goods}
          decorator="categoryCode"
          label="商品/分类"
          placeholder="编码/名称"
          rules={[{required: true, message: '商品分类编码/名称不可为空'}]}
          listRenderGetValue={item => `${item.productCode || item.categoryCode}/${item.productName || item.categoryName}`} // 因为获取不到name，所以采取这种方式
          listRenderGetLabel={item => `${item.productCode || item.categoryCode}/${item.productName || item.categoryName}`}
          {...popupFormItemLayout}
        />
      </div>
    )
  }
}



