import React, {PureComponent} from 'react'
import {
  Table
} from 'antd'
import moment from 'moment'
import {goodsType} from 'yhUtil'
import style from '../style/Table.scss'
import {connect} from 'react-redux'
import {bindActionCreators} from 'redux'
import {handleSearchResult, handleSelectChange, updateSelectedRows, pageSizeChange} from '../redux/reducer'

@connect(
  state => ({...state.splitGroupModel}),
  dispatch => bindActionCreators({handleSearchResult, handleSelectChange, updateSelectedRows, pageSizeChange}, dispatch)
)
export default class TableComponent extends PureComponent {
  constructor() {
    super()
    this.columns = [
      {
        title: '',
        dataIndex: '',
        key: '',
        width: 10
      }, {
        title: '库存地点',
        dataIndex: 'inventoryDcCode',
        key: 'inventoryDcCode',
        width: 150,
        render: (text, record, index) => `${text} ${record.inventoryDcName}`
      }, {
        title: '分组名称',
        dataIndex: 'groupCode',
        key: 'groupCode',
        width: 120,
        render: (text, record, index) => `${text} ${record.groupName}`
      }, {
        title: '类型',
        dataIndex: 'categoryType',
        key: 'categoryType',
        width: 50,

        render: (text, record, index) => goodsType(String(text))
      }, {
        title: '商品/分类',
        dataIndex: 'categoryCode',
        key: 'categoryCode',
        width: 100,
        render: (text, record, index) => `${text} ${record.categoryName}`
      }, {
        title: '最后更新者',
        dataIndex: 'updatedBy',
        key: 'updatedBy',
        width: 50
      }, {
        title: '最新更新时间',
        dataIndex: 'updatedTime',
        key: 'updatedTime',
        width: 80,
        render: text => <span>{moment(text).format('YYYY-MM-DD HH:mm:ss')}</span>
      }
    ]
  }

  render() {
    const {
      pageSizeChange,
      total,
      page,
      size,
      tableLoading,
      handleSelectChange,
      updateSelectedRows,
      selectedRowKeys,
      searchResult
    } = this.props
    /* 选择功能的配置 */
    const rowSelection = {
      selectedRowKeys,
      onChange: handleSelectChange,
      onSelect: updateSelectedRows
    }

    let pagination = {
      total,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      onShowSizeChange: pageSizeChange, // pageSize 变化的回调
      className: `${style.pagination}`,
      onChange: pageSizeChange,
      pageSizeOptions: ['50', '100', '200', '500']
    }

    return (
      <div className={style['table-group']}>
        <Table
          className={style.table}
          rowKey={record => record.id}
          loading={tableLoading}
          rowSelection={rowSelection}
          pagination={pagination}
          dataSource={searchResult}
          columns={this.columns}
          size="middle"
        />
      </div>
    )
  }
}



